function createEnum(definition) {
    const valueMap = {}
    const valMap = {}
    const textMap = {}
    for (const enumName of Object.keys(definition)) {
        const [value, text, val] = definition[enumName]
        valueMap[enumName] = value
        valMap[value] = val
        textMap[value] = text
    }
    return {
        ...valueMap,
        getTextByName(name) {
            return (definition[name] && definition[name][1]) || ''
        },
        getValueByName(name) {
            return definition[name] && definition[name][0] || 0
        },
        getTextByValue(value) {
            return textMap[value] || ''
        },
        getVal(val) {
            // console.log(valMap)
            // console.log(valMap[val])
            return valMap[val] || 0
        },
        Arr() {
            return definition
        }
    }
}

export default {
    // 全局错误码
    errorCode: createEnum({
        ErrUnauthenticated: [10002, '没有权限'],
        ErrMaxQRConvMemberNum: [10004, '面对面分享群人数达到上限'],
        ErrInaccurateAnswer: [10005, '密保问题答案错误'],
    }),
    userRole: createEnum({
        normal: [1, '普通用户'],
        manage: [9, '管理员'],
        admin: [10, '站长'],
    }),

}
