VueRouter.prototype.goBack = function () {
    // this.isBack = true
    window.history.go(-1)
}

Vue.use(VueRouter);

// 字段说明 K--keepAlive(缓存机制), R--requireAuth(是否需要登录), F--isFooter(底部是否显示), I--index(路由返回动画下标,路由层级越深,则index值要越大)
