import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Router from 'vue-router'


Vue.use(Router)
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

const Ym = location.hostname.replace('ais.', '')
const mod = {
    // dev: 'localhost',
    dev: 'https://bd.sdyqiot.com/',
    // dev: '192.168.101.184',
    pud: Ym || 'akid.cloud',
}
Vue.prototype.$BASEAPI = 'https://bd.sdyqiot.com/';
Vue.prototype.$BASECDN = process.env.NODE_ENV == 'development' ? 'http://' + mod.dev + ':3068/' : location.protocol + '//cdn.' + mod.pud + '/';
Vue.prototype.$BASEWEB = process.env.NODE_ENV == 'development' ? 'http://' + mod.dev + ':3060/' : location.protocol + '//' + mod.pud + '/';

import '@/assets/stylus/app.styl'
import {Get, Post} from "./utils/http";


//api
Vue.prototype.$lock = false; //post 请求锁
Vue.prototype.$post = Post;
Vue.prototype.$get = Get;

import CONST from './utils/const'
Vue.prototype.CONST = CONST

Vue.prototype.ROUND = (val, t)=> {
    const x = t ? Math.pow(10, t) : 100
    return Math.round(val * x) / x
};
Vue.prototype.$defForm = {
    status: null,
    type: null,
    page: 1,
    key: null,
    start: null,
    end: null,
};
Vue.prototype.$ppo = {
    shortcuts: [{
        text: '今天',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近一周',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近一个月',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
        }
    }, {
        text: '最近三个月',
        onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
        }
    }]
};



Vue.config.productionTip = false

window.MVUE = new Vue({
    router,
    store,
    data: {
        eventHub: new Vue()
    },
    render: h => h(App),
}).$mount('#app')

